(function() {
  'use strict';

  angular
    .module('forecastAngular')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/sheets/main.html',
        controller: 'SheetsController',
        controllerAs: 'sheets'
      })
      .state('sample', {
        url: '/sample',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'sample'
      })
      .state('login', {
        url: '/login',
        templateUrl: 'app/main/adminLogin.html',
        controller: 'LoginController',
        controllerAs: 'login'
      });
    $urlRouterProvider.otherwise('/');
  }

})();

